<template>
    <div class="layout">
        <Sider :style="{position: 'fixed', height: '100vh', left: 0, overflow: 'auto'}">
            <Menu active-name="1-2" theme="dark" width="auto" :open-names="['1']">
                <Submenu name="1">
                    <template slot="title">
                        <Icon type="ios-navigate"></Icon>
                        图书管理
                    </template>
                    <MenuItem name="1-1">增加图书</MenuItem>
                    <MenuItem name="1-2">图书列表</MenuItem>
                    <MenuItem name="1-3">管理图书</MenuItem>
                </Submenu>
                <Submenu name="2">
                    <template slot="title">
                        <Icon type="ios-keypad"></Icon>
                        图书信息
                    </template>
                    <MenuItem name="2-1">图书借阅</MenuItem>
                    <MenuItem name="2-2">图书归还</MenuItem>
                </Submenu>
                <!-- <Submenu name="3">
                    <template slot="title">
                        <Icon type="ios-analytics"></Icon>
                        三级菜单
                    </template>
                    <MenuItem name="3-1">Option 3</MenuItem>
                    <MenuItem name="3-2">Option 4</MenuItem>
                </Submenu> -->
            </Menu>
        </Sider>
        <Layout :style="{marginLeft: '200px'}">
            <Header :style="{background: '#fff', boxShadow: '0 2px 3px 2px rgba(0,0,0,.1)'}"></Header>
            <Content :style="{padding: '0 16px 16px'}">
                <Breadcrumb :style="{margin: '16px 0'}">
                    <BreadcrumbItem to="/">
                        <Icon type="ios-home-outline"></Icon>
                         HomeHome
                    </BreadcrumbItem>
                    <BreadcrumbItem to="/">
                        <Icon type="social-buffer-outline"></Icon>
                         Components
                    </BreadcrumbItem>
                    <BreadcrumbItem to="/">
                        <Icon type="pound"></Icon>
                            Layout
                    </BreadcrumbItem>
                </Breadcrumb>
                <Card>
                    <div style="height: 300px">
                        <Form></Form>  
                    </div>
                    <div style="height:100px;margin-left: 30%">
                        <Page :total="100" show-elevator></Page> 
                    </div>
                </Card>
            </Content>
        </Layout>
    </div>
</template>
<script>
    import Form from '../form/form'
    export default {
        components:{
            Form
        }
    }
</script>

<style>
    .layout{
        border: 1px solid #d7dde4;
        background: #f5f7f9;
        position: relative;
        border-radius: 4px;
        overflow: hidden;
    }
    .layout-header-bar{
        background: #fff;
        box-shadow: 0 1px 1px rgba(0,0,0,.1);
    }
</style>




