<template>
    <Table border :columns="columns7" :data="data6"></Table>
</template>
<script>
    export default {
        data () {
            return {
                columns7: [
                    {
                        title: '编号',
                        key: 'number'
                    },
                    {
                        title: '书名',
                        key: 'name',
                        render: (h, params) => {
                            return h('div', [
                                h('Icon', {
                                    // props: {
                                    //     type: 'person'
                                    // }
                                }),
                                h('strong', params.row.name)
                            ]);
                        }
                    },
                    {
                        title: '图书编码',
                        key: 'booknumber'
                    },
                    {
                        title: '借书时间',
                        key: 'time'
                    },
                    {
                        title: 'Action',
                        key: 'action',
                        width: 150,
                        align: 'center',
                        render: (h, params) => {
                            return h('div', [
                                h('Button', {
                                    props: {
                                        type: 'primary',
                                        size: 'small'
                                    },
                                    style: {
                                        marginRight: '5px'
                                    },
                                    on: {
                                        click: () => {
                                            this.show(params.index)
                                        }
                                    }
                                }, '详细信息'),
                                h('Button', {
                                    props: {
                                        type: 'error',
                                        size: 'small'
                                    },
                                    on: {
                                        click: () => {
                                            this.remove(params.index)
                                        }
                                    }
                                }, '删除')
                            ]);
                        }
                    }
                ],
                data6: [
                    {
                        number: 18,
                        name: '高等数学',
                        booknumber: 23,
                        time:'2018-02-06'
                    },
                    {
                        number: 22,
                        name: '线性代数',
                        booknumber: 24,
                        time:'2018-02-07'
                    },
                    {
                        name: '大学英语',
                        number:25,
                        booknumber: 26,
                        time:'2018-02-08'
                    },
                    {
                        name: '体育生活',
                        number: 66,
                        booknumber: 28,
                        time:'2018-02-09'
                    }
                ]
            }
        },
        methods: {
            show (index) {
                this.$Modal.info({
                    title: 'User Info',
                    content: `编码：${this.data6[index].number}<br>书名：${this.data6[index].name}
                            <br>图书编码：${this.data6[index].booknumber}<br>借阅时间：${this.data6[index].time}`
                })
            },
            remove (index) {
                this.data6.splice(index, 1);
            }
        }
    }
</script>

   



